@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,700;0,900;1,400&display=swap);
.player{min-height:20vh;display:flex;flex-direction:column;align-items:center;justify-content:space-around}.player .time-control{width:50%;display:flex}.player .time-control input{width:100%;padding:1rem 0rem}.player .time-control p{padding:1rem}.player .play-control{width:25%;display:flex;justify-content:space-between;align-items:center;margin-top:-10px}@media screen and (max-width: 800px){.player .time-control{background:rgba(136,136,136,0.363);width:80vw;border-radius:12px}.player .play-control{width:50%}}.song{padding-top:20px;min-height:70vh;display:flex;flex-direction:column;align-items:center;justify-content:center}.song img{width:20%;border-radius:50%}.song h2{padding:3rem 1rem 1rem 1rem}.song h3{font-size:1.2rem}@media screen and (max-width: 768px){.song img{width:60%}}.library{position:fixed;top:0;left:0;width:20rem;height:100%;background:rgba(209,209,209,0.795);overflow:scroll;box-shadow:2px 2px 50px #cacaca;transform:translateX(-100%);opacity:0;transition:all 0.3s ease-out}.library h2{padding:2rem}.library-song{display:flex;padding:1rem 2rem}.library-song img{width:30%}.library-song:hover{background:#ccc}.library-song .description{padding-left:1rem}.library-song .description h3{font-size:1rem}.library-song .description h4{font-size:0.7rem}*{scrollbar-width:thin;scrollbar-color:rgba(155,155,155,0.5) transparent}* ::-webkit-scrollbar{width:6px}* ::-webkit-scrollbar-track{background:transparent}* ::-webkit-scrollbar-thumb{background-color:rgba(155,155,155,0.5);border:transparent;border-radius:20px}* ::-webkit-scrollbar-thumb:hover{background-color:rgba(155,155,155,0.8)}.selected{background-color:palevioletred}.active-library{transform:translateX(0);opacity:1}@media screen and (max-width: 768px){.library{width:100%}}.nav{display:flex;justify-content:space-around;align-items:center;padding-top:10px}.nav button{background:transparent;border:none;cursor:pointer;border:2px solid #414141;padding:0.5rem;transition:all 0.5s ease}.nav button:hover{background:#414141;color:white}@media screen and (max-width: 768px){nav button{z-index:10}}*{margin:0;padding:0;box-sizing:border-box;font-family:"lato", sans-serif}h1,h2,h3{color:#262626}.App{transition:all 0.5s ease}.library-active{margin-left:20%}

